* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #8efc57;
}
.form {
  background-color: #e9feff;
  width: 100wv;
  height: 300px;
  margin: 10rem auto;
  border-radius: 5px;
}
.form form {
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
}
h2 {
  margin: 1.2rem;
}
input[type="text"] {
  padding: 8px;
  font-size: 16px;
  border-radius: 10px;
  border: 1px solid black;
  outline: none;
}
button {
  margin-top: 0.5rem;
  padding: 15px;
  background-color: #8efc57;
  outline: none;
  border: none;
  font-size: 16px;
  border-radius: 5px;
}
button:hover {
  transform: scale(1.05);
}
h3 {
  margin: 1rem;
}
@media screen and (min-width: 720px) {
  .form {
    width: 500px;
  }
  h2 {
    margin: 2.5rem;
  }
}
